import divider from '@/components/divider.vue';
export default {
	components: {
		divider
	},
	data() {
		return {
			background: {
				backgroundColor: '#fff',
			},
			params: {
				year: true,
				month: true,
				day: true,
				hour: true,
				minute: true,
				second: false,
				province: true,
				city: true,
				area: true,
				timestamp: false, // 1.3.7版本提供
			},
			open: false,
			open2: false,
			open3: false,
			index: -1,
			index2: -1,
			index3: -1,
			musicName:"无",
			startTime: "选择开始时间",
			endTime: "选择结束时间",
			startTime2: "选择开始时间",
			endTime2: "选择结束时间",
			startTime3: "选择开始时间",
			endTime3: "选择结束时间",
			showTime: false,
			showTime2: false,
			showTime3: false,
			value1: '代金券',
			value2: '指定日期',
			value3: '代金券',
			value4: '指定日期',
			activityDetail:"",
			activityRule:"",
			imageList:[],
			imageUrls:"",
			list: [{
					name: '代金券',
					disabled: false
				},
				{
					name: '折扣券',
					disabled: false
				},
				{
					name: '兑换券',
					disabled: false
				},
			],
			list2: [{
					name: '指定日期',
					disabled: false
				},
				{
					name: '有效天数',
					disabled: false
				},
			],
			list3: [{
					name: '代金券',
					disabled: false
				},
				{
					name: '折扣券',
					disabled: false
				},
				{
					name: '兑换券',
					disabled: false
				},
			],
			list4: [{
					name: '指定日期',
					disabled: false
				},
				{
					name: '有效天数',
					disabled: false
				},
			],
		}
	},
	methods: {
		change(status) {
			console.log(status)
			this.open = status
		},
		
		change2(status) {
			console.log(status)
			this.open2 = status
		},
		
		change3(status) {
			console.log(status)
			this.open3 = status
		},
		
		// 选择营业时间
		selectTime(num) {
			this.showTime = true
			if (num == 1) {
				this.index = 1
			} else {
				this.index = 2
			}
		},
		chooseTime(res) {
			console.log(res)
			if (this.index == 1) {
				this.startTime = res.year + "-" + res.month + "-" + res.day + " " + res.hour + "-" + res.minute;
			} else {
				this.endTime = res.year + "-" + res.month + "-" + res.day + " " + res.hour + "-" + res.minute;
			}
		},
		
		// 选择营业时间
		selectTime2(num) {
			this.showTime2 = true
			if (num == 1) {
				this.index2 = 1
			} else {
				this.index2 = 2
			}
		},
		chooseTime2(res) {
			console.log(res)
			if (this.index2 == 1) {
				this.startTime2 = res.year + "-" + res.month + "-" + res.day + " " + res.hour + "-" + res.minute;
			} else {
				this.endTime2 = res.year + "-" + res.month + "-" + res.day + " " + res.hour + "-" + res.minute;
			}
		},
		
		// 选择营业时间
		selectTime3(num) {
			this.showTime3 = true
			if (num == 1) {
				this.index3 = 1
			} else {
				this.index3 = 2
			}
		},
		chooseTime3(res) {
			console.log(res)
			if (this.index3 == 1) {
				this.startTime3 = res.year + "-" + res.month + "-" + res.day + " " + res.hour + "-" + res.minute;
			} else {
				this.endTime3 = res.year + "-" + res.month + "-" + res.day + " " + res.hour + "-" + res.minute;
			}
		},
		
		
		chooseImage(){
			if(this.imageList.length<=9){
				uni.chooseImage({
				   count: 9, //默认9
				   success: (res) => {
				   	console.log(res.tempFilePaths);
					this.imageUrls=res.tempFilePaths
					for (var i = 0; i < res.tempFilePaths.length; i++) {
						let data={
							type:"pic",
							src:res.tempFilePaths[i],
						}
						this.imageList.push(data)
					}
					
				   },
				   fail: (err) => {
				   	console.log(err)
				   }
				});
			}
			else{
				uni.showToast({
					icon:"none",
					title:"最多上传9张"
				})
			}
		
		},
		
		previewImage(url){
			uni.previewImage({
				current:url,
				urls:this.imageUrls,
				indicator:"default",
				success: (res) => {
					
				},
				fail: (err) => {
					console.log(err)
				}
			})
		}
	}
}
